'use client'

import useSWR from 'swr'
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card'
import { BarChart, Bar, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer } from 'recharts'
import { Calendar, DollarSign, Package, Users } from 'lucide-react'
import { format, subDays } from 'date-fns'

const fetcher = async (url: string) => {
    const res = await fetch(url)
    const text = await res.text()
    try {
        return JSON.parse(text)
    } catch (e) {
        console.error(`JSON Parse Error for ${url}:`, text)
        throw e
    }
}

export default function AdminDashboardPage() {
    const { data: bookings, error: bookingsError } = useSWR('/api/bookings', fetcher)
    const { data: services, error: servicesError } = useSWR('/api/services', fetcher)

    if (bookingsError || servicesError) return <div>Failed to load dashboard data</div>
    if (!bookings || !services) return <div>Loading dashboard...</div>

    // Calculate Stats
    const totalBookings = bookings.length
    const confirmedBookings = bookings.filter((b: any) => b.status === 'CONFIRMED').length
    const totalRevenue = bookings
        .filter((b: any) => b.status === 'CONFIRMED')
        .reduce((acc: number, curr: any) => acc + (curr.totalPrice || 0), 0)
    const activeServices = services.length

    // Prepare Chart Data (Last 7 days)
    const chartData = Array.from({ length: 7 }).map((_, i) => {
        const date = subDays(new Date(), 6 - i)
        const dateStr = format(date, 'yyyy-MM-dd')
        const dayBookings = bookings.filter((b: any) =>
            format(new Date(b.date), 'yyyy-MM-dd') === dateStr
        ).length
        return {
            name: format(date, 'MMM dd'),
            bookings: dayBookings,
        }
    })

    return (
        <div className="space-y-8">
            <h1 className="text-3xl font-bold">Dashboard Overview</h1>

            {/* Stats Cards */}
            <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-4">
                <Card>
                    <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                        <CardTitle className="text-sm font-medium">Total Revenue</CardTitle>
                        <DollarSign className="h-4 w-4 text-muted-foreground" />
                    </CardHeader>
                    <CardContent>
                        <div className="text-2xl font-bold">${totalRevenue.toLocaleString()}</div>
                        <p className="text-xs text-muted-foreground">
                            From confirmed bookings
                        </p>
                    </CardContent>
                </Card>
                <Card>
                    <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                        <CardTitle className="text-sm font-medium">Bookings</CardTitle>
                        <Calendar className="h-4 w-4 text-muted-foreground" />
                    </CardHeader>
                    <CardContent>
                        <div className="text-2xl font-bold">{totalBookings}</div>
                        <p className="text-xs text-muted-foreground">
                            {confirmedBookings} confirmed
                        </p>
                    </CardContent>
                </Card>
                <Card>
                    <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                        <CardTitle className="text-sm font-medium">Active Services</CardTitle>
                        <Package className="h-4 w-4 text-muted-foreground" />
                    </CardHeader>
                    <CardContent>
                        <div className="text-2xl font-bold">{activeServices}</div>
                        <p className="text-xs text-muted-foreground">
                            Available for booking
                        </p>
                    </CardContent>
                </Card>
                <Card>
                    <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                        <CardTitle className="text-sm font-medium">Conversion Rate</CardTitle>
                        <Users className="h-4 w-4 text-muted-foreground" />
                    </CardHeader>
                    <CardContent>
                        <div className="text-2xl font-bold">
                            {totalBookings > 0
                                ? Math.round((confirmedBookings / totalBookings) * 100)
                                : 0}%
                        </div>
                        <p className="text-xs text-muted-foreground">
                            Booking confirmation rate
                        </p>
                    </CardContent>
                </Card>
            </div>

            {/* Charts & Recent Activity */}
            <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-7">
                <Card className="col-span-4">
                    <CardHeader>
                        <CardTitle>Bookings Overview</CardTitle>
                    </CardHeader>
                    <CardContent className="pl-2">
                        <ResponsiveContainer width="100%" height={350}>
                            <BarChart data={chartData}>
                                <CartesianGrid strokeDasharray="3 3" vertical={false} />
                                <XAxis
                                    dataKey="name"
                                    stroke="#888888"
                                    fontSize={12}
                                    tickLine={false}
                                    axisLine={false}
                                />
                                <YAxis
                                    stroke="#888888"
                                    fontSize={12}
                                    tickLine={false}
                                    axisLine={false}
                                    tickFormatter={(value) => `${value}`}
                                />
                                <Tooltip
                                    cursor={{ fill: 'transparent' }}
                                    contentStyle={{ borderRadius: '8px', border: 'none', boxShadow: '0 4px 12px rgba(0,0,0,0.1)' }}
                                />
                                <Bar dataKey="bookings" fill="#000000" radius={[4, 4, 0, 0]} />
                            </BarChart>
                        </ResponsiveContainer>
                    </CardContent>
                </Card>

                <Card className="col-span-3">
                    <CardHeader>
                        <CardTitle>Recent Bookings</CardTitle>
                    </CardHeader>
                    <CardContent>
                        <div className="space-y-8">
                            {bookings.slice(0, 5).map((booking: any) => (
                                <div key={booking.id} className="flex items-center">
                                    <div className="ml-4 space-y-1">
                                        <p className="text-sm font-medium leading-none">{booking.name}</p>
                                        <p className="text-sm text-muted-foreground">
                                            {booking.activityTitle}
                                        </p>
                                    </div>
                                    <div className="ml-auto font-medium">
                                        {booking.status === 'CONFIRMED' ? (
                                            <span className="text-green-600">Confirmed</span>
                                        ) : booking.status === 'CANCELLED' ? (
                                            <span className="text-red-600">Cancelled</span>
                                        ) : (
                                            <span className="text-yellow-600">Pending</span>
                                        )}
                                    </div>
                                </div>
                            ))}
                            {bookings.length === 0 && (
                                <p className="text-sm text-muted-foreground">No recent bookings.</p>
                            )}
                        </div>
                    </CardContent>
                </Card>
            </div>
        </div>
    )
}
